<template >
  <v-layout id="app">
         <v-menu
            v-model="menu"
            ref="menu"
            :close-on-content-click="false"
            :return-value.sync="dataValue.datePass"
            :nudge-right="10"
            @input="updateDate()"
            lazy
            transition="scale-transition"
            min-width="290px"
            class="flex md12 lg12 xs12"
            >
            <v-text-field
               slot="activator"
               :value="dataValue.datePass[0]==undefined?'Select a date':dataValue.datePass[0]+'/'+dataValue.datePass[dataValue.datePass.length-1]"
               label="Date"
               append-icon="event"
               readonly
               style="font-size:13px"
               @input="retrunArray"

               >
            </v-text-field>
            <!-- <v-text-field
            slot="activator"
            label="Trip Date"
            append-icon=""
            :value=valuePassed
            readonly=""
            flat
            solo
            class="dateTextFeild filterLabelColor textFeildTheme mylightTheme mymsg"
            >
            </v-text-field> -->
            <v-date-picker
               v-model="dataValue.datePass"
               ref="datePicker" 
               :value="dataValue.datePass" 
               @input="updateDate()"
               multiple
               color=red
               :min=min
               :max=max
               @change="retrunArray"
               >
            </v-date-picker>
            <v-spacer></v-spacer>
            <v-layout style="background-color:#ffffff" align-center justify-end row fill-height>
               <v-btn flat color="#000" @click="menu = false">Cancel</v-btn>
               <v-btn flat color="#000" @click="$refs.menu.save(dataValue.datePass),save(dataValue.datePass),updateDate()">OK</v-btn>
            </v-layout>
         </v-menu>
  </v-layout>
</template>

<script>
   /* eslint-disable */
       import moment from 'moment'
       function enumerateDaysBetweenDates(startDate, endDate) {
           var dates = [];
           dates.push(startDate.toDate().toISOString().substr(0, 10));
           startDate = startDate.add(1, 'days');
           // endDate = endDate.add(1, 'days');
           while (startDate.format('M/D/YYYY') !== endDate.format('M/D/YYYY')) {
            //console.log("startDate.toDate()", startDate.toDate());
               dates.push(startDate.toDate().toISOString().substr(0, 10));
               startDate = startDate.add(1, 'days');
           }
           dates.push(endDate.toDate().toISOString().substr(0, 10));
          //console.log("dates..............",dates)
           return dates;
       }
       export default {
          name: 'app',
          props: ['dateRangeValue','color','min','max'],
         data: function () {
            return {
               dataValue: {
                 /* new Date().toISOString().substr(0, 10) */
                   datePass: []

               },
               menu: false,

            }},
            props:{
                dateRangeValue: Object,
                startDate:Object,
                endDate:Object,
                color:String,
                min:String,
                max:String
            },
           computed: {
               retrunArray () {
                //console.log("///////////////////////",this.dataValue.datePass,this.dataValue.datePass.length)
                 if(this.dataValue.datePass[1] && this.dataValue.datePass.length==2){  
                   
                     var fromDate = moment(new Date(this.dataValue.datePass[0]))
                     var toDate = moment(new Date(this.dataValue.datePass[1]))
                    //console.log('from to', this.dataValue.datePass[0], this.dataValue.datePass[1])
                     if(fromDate > toDate){
                      //console.log('greater')
                       var resGre = enumerateDaysBetweenDates(toDate, fromDate)
                      //console.log("res", res);
                       this.dataValue.datePass = resGre;
                       resGre=[];
                     }
                     else{
                        //console.log('lesser')
                          var resLes = enumerateDaysBetweenDates(fromDate, toDate)
                        //console.log("res", res);
                         this.dataValue.datePass = resLes;
                         resLes=[];
                     }
                 }
                 else if(this.dataValue.datePass.length >=3){
                   ////console.log("33333",this.dataValue.datePass)
                   this.dataValue.datePass=[]
                  //console.log("33333######", this.$refs.datePicker.inputDate)
                   var data=[]
                   data.push(this.$refs.datePicker.inputDate)
                  //console.log("Update array",data)
                    this.dataValue.datePass=data
                   //  this.dataValue.datePass.push(this.$refs.datePicker.inputDate)
                   /* var passValue=[]
                   passValue.push(this.dataValue.datePass[this.dataValue.datePass.length-1]);
                   this.dataValue.datePass=passValue;
                   passValue=[]; */
                 }
                 else{
                  //console.log("elseeeeeeeee")
                 }
                 return this.dataValue.datePass
               }
           },
           methods: {
               save(filterData) {
               //console.log('data from Filter', filterData);
                //   this.valuePassed=dataValue.datePass[0]+'/'+dataValue.datePass[dataValue.datePass.length-1]
                this.valuePassed=filterData[0]+'/'+filterData[filterData.length-1]
               },
                updateDate() {
                    this.$emit('input', {
                      dateRangeValue: {
                          array:this.dataValue.datePass,
                       },
                        startDate: { startDate:moment(this.dataValue.datePass[0]).format()},
                        endDate: { endDate:moment(this.dataValue.datePass[this.dataValue.datePass.length-1]).format()}

                    })
               }
          }
    }
</script>



